.add-goods {
    position: relative;
    padding-top: 119px;
    padding-bottom: 86px;
    border-top: 1px solid #68b738;
    .add-goods__wrapper {
        max-width: 1300px;
        width: 100%;
        margin: 0 auto;
        padding-left: 40px;
        padding-right: 40px; }
    .add-goods__title {
        min-width: 418px;
        background-color: $white;
        position: absolute;
        top: -21px;
        transform: translateX(-40px);
        font-size: 36px;
        font-weight: 400;
        line-height: 36px;
        text-align: center; }

    .add-goods__block-wrapper {
        display: inline-grid;
        grid-template-columns: 896px 245px;
        grid-column-gap: 78px;
        .add-goods__items {
            margin-bottom: -15px;
            .add-goods__item {
                display: inline-grid;
                grid-template-columns: minmax(322px, auto) minmax(245px, auto) minmax(82px, auto) minmax(247px, auto);
                margin-bottom: 32px;
                .add-good-item__goods {
                    padding-top: 10px;
                    padding-bottom: 10px;
                    font-size: 20px;
                    line-height: 20px;
                    text-transform: uppercase;
                    vertical-align: center; }
                .add-good-item__volume, .add-good-item__price {
                    padding-top: 10px;
                    padding-bottom: 10px;
                    font-family: "Arial MT";
                    font-size: 16px;
                    line-height: 20px;
                    color: $arial-color; }
                .add-good-item__button {
                    min-height: 40px;
                    font-family: Oswald;
                    font-size: 16px;
                    line-height: 20px;
                    text-transform: uppercase;
                    color: $white;
                    background-color: #68b738;
                    cursor: pointer; } } }

        .add-goods__gift {
            min-height: 289px;
            padding-top: 65px;
            padding-bottom: 43px;
            padding-left: 43px;
            padding-right: 43px;
            background-image: url(../img/gift-photo-desktop.png);
            text-align: center;
            .add-goods-gift__image {
                margin-bottom: 53px;
                fill: white; }
            .add-goods-gift__description {
                font-family: "Arial MT";
                font-size: 16px;
                font-weight: 400;
                line-height: 20px;
                color: $white; } } } }

@media screen and (max-width: 1299.98px) {
    .add-goods {
        position: relative;
        padding-top: 94px;
        padding-bottom: 76px;
        border-top: 3px solid #68b738;
        .add-goods__wrapper {
            padding-left: 30px;
            padding-right: 30px;
            .add-goods__title {
                top: -25px;
                transform: translateX(9px); } }

        .add-goods__block-wrapper {
            display: grid;
            grid-template-columns: 1fr;
            grid-column-gap: 0px;
            grid-row-gap: 69px;
            .add-goods__items {
                display: grid;
                justify-content: center;
                margin-bottom: 0px;
                .add-goods__item {
                    display: inline-grid;
                    grid-template-columns: minmax(407px, auto) minmax(184px, auto) auto;
                    grid-row-gap: 16px;
                    min-width: 708px;
                    margin-bottom: 0px;
                    padding: 26px 38px;
                    border: 1px solid #cdcdcd;
                    .add-good-item__button {
                        grid-column-start: 1;
                        grid-column-end: 4;
                        min-height: 40px;
                        font-family: Oswald;
                        font-size: 16px;
                        line-height: 20px;
                        text-transform: uppercase;
                        color: $white;
                        background-color: #68b738;
                        cursor: pointer; } } }

            .add-goods__gift {
                display: grid;
                grid-template-columns: 1fr 164px;
                min-height: 200px;
                max-height: 200px;
                padding-top: 65px;
                padding-bottom: 75px;
                padding-left: 91px;
                padding-right: 125px;
                text-align: center;
                .add-goods-gift__image {
                    margin-bottom: 0px; }
                .add-goods-gift__description {
                    text-align: left; } } } } }


@media screen and (max-width: 767.98px) {
    .add-goods {
        padding-top: 30px;
        padding-bottom: 35px;
        border-top: 3px solid #68b738;
        .add-goods__wrapper {
            padding-left: 20px;
            padding-right: 20px;
            .add-goods__title {
                min-width: 272px;
                top: -18px;
                transform: translateX(-25px);
                font-size: 24px;
                line-height: 24px;
                text-align: center; } }

        .add-goods__block-wrapper {
            display: grid;
            grid-template-columns: 1fr;
            grid-column-gap: 0px;
            grid-row-gap: 39px;
            .add-goods__items {
                .add-goods__item {
                    display: inline-grid;
                    grid-gap: 15px;
                    grid-template-columns: 1fr 1fr;
                    min-width: 100%;
                    padding: 14px 0px 17px 0px;
                    border-left: none;
                    border-right: none;
                    .add-good-item__goods {
                        grid-column-start: 1;
                        grid-column-end: 4;
                        grid-row-start: 1;
                        grid-row-end: 2;
                        padding: 0; }
                    .add-good-item__volume {
                        padding: 0; }
                    .add-good-item__price {
                        padding: 0;
                        text-align: right; } } }

            .add-goods__gift {
                display: grid;
                grid-template-columns: 1fr;
                min-height: 289px;
                max-height: 289px;
                padding-top: 65px;
                padding-bottom: 43px;
                padding-left: 60px;
                padding-right: 60px;
                text-align: center;
                .add-goods-gift__image {
                    margin: 0 auto;
                    margin-bottom: 0px; }
                .add-goods-gift__description {
                    text-align: center; } } } } }




