.product {
    padding-top: 28px;
    padding-bottom: 79px; }

.product__wrapper {
    max-width: 1300px;
    width: 100%;
    margin: 0 auto;
    padding-left: 40px;
    padding-right: 40px;
    .product__title {
        margin-bottom: 156px; } }


.cat-energy-products__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gap: 80px;
    grid-row-gap: 142px;
    .cat-energy-products__item {
        display: grid;
        max-width: 245px;
        padding-top: 167px;
        padding-bottom: 40px;
        padding-right: 39px;
        padding-left: 39px;
        position: relative;
        background-color: #f2f2f2;
        .cat-energy-item__title {
            margin: 0;
            margin-bottom: 10px;
            text-align: center;
            text-transform: uppercase; }
        .cat-energy-item__image {
            position: absolute;
            top: -35px;
            left: 50%;
            transform: translateX(-50%); }
        .cat-energy-item__image-big {
            position: absolute;
            top: -70px;
            left: 50%;
            transform: translateX(-50%); }
        .cat-energy-item__description {
            margin-bottom: 17px;
            font-family: "Arial MT";
            font-size: 16px;
            font-weight: 400;
            line-height: 16px;
            color: $arial-color;
            .cat-energy-description__mass, .cat-energy-description__taste, .cat-energy-description__price {
                display: flex;
                justify-content: space-between;
                padding-top: 9px;
                padding-bottom: 9px;
                border-bottom: 1px solid #cdcdcd; } }
        .cat-energy-item__button {
            max-width: 169px;
            min-height: 40px;
            font-size: 16px;
            line-height: 20px;
            text-transform: uppercase;
            font-family: Oswald;
            background-color: #68b738;
            color: $white;
            cursor: pointer; }
        .cat-energy-item__button--all {
            background-color: #f2f2f2;
            color: #444444;
            border: none;
            max-height: 40px;
            align-self: end;
            padding-top: 3px; } }

    .cat-energy-products__item--all {
        background-color: transparent;
        border: 1px solid #f2f2f2;
        .cat-energy-item__plus {
            position: absolute;
            top: 55px;
            left: 50%;
            transform: translateX(-50%); }
        .cat-energy-description__all {
            text-align: center;
            font-size: 18px; } } }

@media screen and (max-width: 1299.98px) {
    .product {
        padding-top: 30px; }

    .product__wrapper {
        padding-left: 30px;
        padding-right: 30px; }

    .cat-energy-products__items {
        grid-template-columns: auto auto;
        grid-column-gap: 60px;
        grid-row-gap: 140px;
        justify-content: space-around;
        .cat-energy-products__item {
            min-width: 324px;
            .cat-energy-item__button {
                width: 100%;
                min-width: 100%;
                min-height: 40px;
                font-size: 16px;
                line-height: 20px;
                text-transform: uppercase;
                font-family: Oswald;
                background-color: #68b738;
                color: $white;
                cursor: pointer; }
            .cat-energy-item__button--all {
                background-color: #f2f2f2;
                color: #444444;
                border: none;
                max-height: 40px;
                align-self: end;
                padding-top: 3px; } } } }

@media screen and (max-width: 767.98px) {
    br {
        display: none; }
    .product {
        padding-top: 32px;
        padding-bottom: 49px; }

    .product__wrapper {
        padding-left: 20px;
        padding-right: 20px;
        .product__title {
            margin-bottom: 35px;
            font-size: 36px;
            line-height: 60px; } }

    .cat-energy-products__items {
        display: grid;
        grid-template-columns: auto;
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        .cat-energy-products__item {
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: auto auto;
            min-width: 100%;
            padding-top: 0;
            padding-bottom: 25px;
            padding-right: 0;
            padding-left: 0;
            position: relative;
            background-color: transparent;
            border-bottom: 1px solid #f2f2f2;
            .cat-energy-item__title {
                grid-column-start: 2;
                grid-column-end: 3;
                grid-row-start: 1;
                grid-row-end: 2;
                margin: 0;
                max-width: 72px;
                margin-bottom: 0px;
                padding-top: 25px;
                text-align: start;
                text-transform: uppercase;
                font-size: 16px;
                font-weight: 400;
                line-height: 20px;
                text-transform: uppercase; }
            .cat-energy-item__image {
                grid-column-start: 1;
                grid-column-end: 2;
                position: static;
                top: 0;
                left: 0;
                transform: none; }
            .cat-energy-item__image-big {
                grid-column-start: 1;
                grid-column-end: 2;
                position: static;
                top: 0;
                left: 0;
                transform: none; }
            .cat-energy-item__description {
                grid-column-start: 2;
                grid-column-end: 3;
                grid-row-start: 1;
                grid-row-end: 2;
                padding-top: 75px;
                font-size: 12px;
                font-weight: 400;
                line-height: 16px;
                margin-bottom: 0;
                .cat-energy-description__mass, .cat-energy-description__taste, .cat-energy-description__price {
                    display: flex;
                    justify-content: space-between;
                    padding-top: 2px;
                    padding-bottom: 2px;
                    border-bottom: none; } }

            .cat-energy-item__button {
                grid-column-start: 1;
                grid-column-end: 3;
                grid-row-start: 2;
                grid-row-end: 3;
                margin-top: 12px; } }

        .cat-energy-products__item--all {
            display: grid;
            grid-template-columns: auto;
            grid-template-rows: minmax(30px, auto) minmax(17px, auto) minmax(13px, auto) minmax(40px, auto);
            border-left: none;
            border-right: none;
            picture {
                grid-column-start: 1;
                grid-column-end: 3;
                grid-row-start: 1;
                grid-row-end: 2; }
            .cat-energy-item__plus {
                grid-column-start: 1;
                grid-column-end: 3;
                grid-row-start: 1;
                grid-row-end: 2;
                position: static;
                display: block;
                margin: 0 auto;
                padding-top: 27px;
                padding-bottom: 27px;
                transform: translateX(0px); }
            .cat-energy-item__title--all {
                grid-column-start: 1;
                grid-column-end: 3;
                grid-row-start: 2;
                grid-row-end: 3;
                max-width: 100%;
                padding-top: 0;
                margin-bottom: 11px;
                text-align: center; }
            .cat-energy-item__description {
                padding: 0px;
                grid-column-start: 1;
                grid-column-end: 3;
                grid-row-start: 3;
                grid-row-end: 4;
                margin-bottom: 22px;
                .cat-energy-description__all {
                    font-size: 12px; } }
            .cat-energy-item__button--all {
                grid-column-start: 1;
                grid-column-end: 3;
                grid-row-start: 4;
                grid-row-end: 5; } } } }

