.form-page__wrapper {
    max-width: 1300px;
    width: 100%;
    margin: 0 auto;
    padding-left: 40px;
    padding-right: 40px;
    .body-program__title {
        margin-bottom: 50px; }
    .body-program__subtitle {
        max-width: 332px;
        margin-bottom: 69px;
        font-size: 20px;
        font-weight: 400;
        line-height: 25px;
        text-transform: uppercase;
        color: $light-black; } }

.form-page__wrapper--first {
    display: grid;
    grid-template-columns: 580px 570px;
    grid-gap: 80px;
    margin-bottom: 91px;
    .body-program__name-wrapper {
        display: grid;
        grid-template-columns: auto 1fr;
        grid-row-gap: 20px;
        align-items: center;
        .body-program__input {}
        .body-program__input:nth-child(2) {
            min-width: 506px;
            margin-left: -57px; }
        .body-program__input:nth-child(4) {
            min-width: 475px;
            margin-left: -33px; } }
    .body-program__info-wrapper {
        display: grid;
        align-items: center;
        min-height: 196px;
        padding-top: 15px;
        padding-bottom: 15px;
        padding-left: 28px;
        border: 1px solid #cdcdcd;
        .body-program__label {
            display: block;
            position: relative;
            cursor: pointer;
            user-select: none;
            input {
                position: absolute;
                top: 0px;
                left: 0;
                opacity: 0;
                cursor: pointer;
                width: 100%;
                min-height: 40px; }
            .body-program__description {
                padding-left: 88px; }
            &:hover input ~ .body-program__checkmark {
                border: 1px solid red; }
            input:checked ~ .body-program__checkmark {}
            input:checked ~ .body-program__checkmark:after {
                display: block; }
            .body-program__checkmark:after {
                top: 10px;
                left: 10px;
                width: 18px;
                height: 18px;
                border-radius: 50%;
                background-color: #68b738; } } } }

.body-program__form {
    .body-program__label {
        color: #222222;
        font-size: 20px;
        font-weight: 400;
        line-height: 20px;
        text-transform: uppercase; }
    .body-program__input {
        min-height: 52px;
        padding-left: 20px;
        border: 1px solid #cdcdcd;
        margin-left: 7px;
        color: $arial-color;
        font-family: Oswald;
        font-size: 20px;
        font-weight: 400;
        line-height: 20px;
        text-transform: uppercase; }
    .body-program__checkmark {
        position: absolute;
        top: -6px;
        left: 0;
        height: 40px;
        width: 40px;
        background-color: $white;
        border: 1px solid #cdcdcd;
        border-radius: 50%;
        z-index: -1;
        &:after {
            content: '';
            position: absolute;
            display: none; } } }

.body-program___contacts-block {
    position: relative;
    border-top: 3px solid #68b738;
    .form-page__wrapper--second {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 78px;
        position: relative;
        padding-top: 87px;
        padding-bottom: 89px;
        .body-program__form__title {
            position: absolute;
            top: -25px;
            left: 0;
            min-width: 593px;
            font-size: 36px;
            font-weight: 400;
            line-height: 36px;
            color: $light-black;
            background-color: $white;
            text-align: center; }
        .body-program__label {
            display: grid;
            grid-template-columns: 1fr 9fr;
            align-items: center;
            .body-program__contacts-description {
                color: #222222; }
            .body-program__input {
                position: relative;
                padding-left: 20px;
                padding-right: 20px; } }
        .body-program__label--email, .body-program__label--phone {
            position: relative; }
        .body-program__label--email:after {
            position: absolute;
            top: 13px;
            right: 20px;
            content: url(../img/form-mail.png);
            width: 17px;
            height: 13px; }
        .body-program__label--phone:after {
            position: absolute;
            top: 13px;
            right: 20px;
            content: url(../img/form-phone.png);
            width: 17px;
            height: 13px; } } }

.body-program___comments-block {
    border-top: 3px solid #68b738;
    .form-page__wrapper--third {
        position: relative;
        padding-top: 87px;
        padding-bottom: 89px;
        .body-program__form__title {
            position: absolute;
            top: -26px;
            left: 0;
            min-width: 263px;
            color: #111111;
            font-size: 36px;
            font-weight: 400;
            line-height: 36px;
            text-align: center;
            background-color: $white; }
        .body-programm__textarea {
            min-width: 100%;
            max-height: 174px;
            padding-top: 17px;
            padding-left: 20px;
            resize: none;
            color: $arial-color;
            font-family: Oswald;
            font-size: 20px;
            font-weight: 400;
            line-height: 30px;
            text-transform: uppercase; } } }

.body-program___extra-block {
    border-top: 3px solid #68b738;
    .form-page__wrapper--four {
        position: relative;
        padding-top: 79px;
        padding-bottom: 80px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-row-gap: 61px;
        justify-content: space-between;
        .body-program__form__title {
            position: absolute;
            top: -26px;
            left: 20px;
            min-width: 263px;
            color: #111111;
            font-size: 36px;
            font-weight: 400;
            line-height: 36px;
            text-align: center;
            background-color: $white; } } }

.extra-block__label {
    display: block;
    min-height: 40px;
    position: relative;
    cursor: pointer;
    user-select: none;
    padding-top: 11px;
    padding-bottom: 11px;
    .extra-block__input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 100%;
        min-height: 40px;
        z-index: 5; }
    .extra-block__label-description {
        padding-left: 60px;
        text-transform: uppercase; } }

.extra-block__chekmark {
    position: absolute;
    top: 3px;
    left: 0;
    width: 40px;
    height: 40px;
    border: 1px solid #cdcdcd; }


.extra-block__label:hover .extra-block__input ~ .extra-block__chekmark {
    border: 1px solid red; }

.extra-block__label .extra-block__input:checked ~ .extra-block__chekmark {
    border: 1px solid red; }

.extra-block__chekmark:after {
    content: '';
    position: absolute;
    display: none; }

.extra-block__label .extra-block__input:checked ~ .extra-block__chekmark:after {
    display: block; }

.extra-block__label .extra-block__chekmark:after {
    content: url(../img/tick.png);
    top: 8px;
    left: 9px; }

.extra-block__button {
    align-self: center;
    grid-column-start: 1;
    grid-column-end: 3;
    min-width: 568px;
    background-color: #68b738;
    min-height: 52px;
    max-height: 52px;
    color: #ffffff;
    font-family: Oswald;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    text-transform: uppercase; }

.extra-block__subtitle {
    align-self: center;
    padding-left: 82px; }

@media screen and (max-width: 1299.98px) {
    .form-page__wrapper {
        padding-left: 30px;
        padding-right: 30px; }
    .body-program {
        padding-top: 28px; }

    .form-page__wrapper--first {
        display: grid;
        grid-template-columns: 677px;
        justify-content: flex-start;
        grid-gap: 0px;
        grid-row-gap: 72px;
        margin-bottom: 91px;
        .body-program__name-wrapper {
            justify-content: center;
            .body-program__input {
                max-width: 567px; }
            .body-program__input:nth-child(2) {
                min-width: 506px;
                max-width: 633.25px;
                margin-left: -57px; }
            .body-program__input:nth-child(4) {
                min-width: 475px;
                max-width: 608px;
                margin-left: -33px; } }
        .body-program__info-wrapper {
            max-width: 677px; } }

    .body-program___contacts-block {
        .form-page__wrapper--second {
            grid-template-columns: 1fr;
            grid-row-gap: 20px;
            .body-program__form__title {
                left: 30px; }
            .body-program__label--email {
                max-width: 718px;
                .body-program__input {
                    margin-left: -6px;
                    max-width: 643.2px; } }
            .body-program__label--phone {
                max-width: 718px;
                .body-program__input {
                    max-width: 617.717px; } } } }

    .body-program___comments-block {
        .form-page__wrapper--third {
            .body-program__form__title {
                left: 30px; } } }

    .body-program___extra-block {
        .form-page__wrapper--four {
            grid-template-columns: 1fr 1fr;
            grid-row-gap: 32px;
            padding-bottom: 72px;
            .body-program__form__title {
                left: 30px; }
            .extra-block__button {
                grid-column-start: 1;
                grid-column-end: 2;
                min-width: 283px;
                max-width: 283px; }
            .extra-block__subtitle {
                transform: translateX(-40px);
                padding-left: 0; } } } }

.form-page .main-contacts__adress-block {
    background-color: #f2f2f2; }

@media screen and (max-width: 767.98px) {
    .form-page__wrapper {
        padding-left: 20px;
        padding-right: 20px; }
    .form-page__wrapper {
        .body-program__title {
            font-size: 36px;
            margin-bottom: 35px; }
        .body-program__subtitle {
            margin-bottom: 39px;
            font-size: 14px;
            font-weight: 400;
            line-height: 18px; } }

    .body-program__form {
        .body-program__label {
            font-size: 16px;
            font-weight: 400;
            line-height: 18px; } }

    .form-page__wrapper--first {
        display: grid;
        grid-template-columns: 1fr;
        justify-content: flex-start;
        grid-gap: 0px;
        grid-row-gap: 23px;
        margin-bottom: 53px;
        .body-program__name-wrapper {
            .body-program__input {
                min-width: 188px;
                max-width: 188px; }
            .body-program__input:nth-child(2) {
                min-width: 238px;
                max-width: 238px;
                margin-left: -43px; }
            .body-program__input:nth-child(4) {
                min-width: 220px;
                max-width: 220px;
                margin-left: -25px; } } }

    .form-page__wrapper--first {
        .body-program__info-wrapper {
            .body-program__label {
                .body-program__description {
                    padding-left: 58px; } } } }


    .body-program__form .body-program__checkmark {
        top: -10px; }

    .body-program___contacts-block .form-page__wrapper--second .body-program__form__title {
        left: 0px;
        padding-left: 20px;
        font-size: 24px;
        line-height: 24px;
        min-width: 215px;
        max-width: 215px;
        text-align: left; }

    .body-program___contacts-block .form-page__wrapper--second .body-program__label--email .body-program__input {
        max-width: 231px;
        min-width: 231px;
        margin-left: 7px; }

    .body-program___contacts-block .form-page__wrapper--second .body-program__label--phone .body-program__input {
        max-width: 210px;
        min-width: 210px; }

    .body-program___contacts-block .form-page__wrapper--second {
        padding-top: 64px;
        padding-bottom: 48px; }

    .body-program___contacts-block .form-page__wrapper--second .body-program__label--email {
        max-width: 283.65px;
        min-width: 283.65px; }

    .body-program___contacts-block .form-page__wrapper--second .body-program__label--phone {
        max-width: 283.65px;
        min-width: 283.65px; }

    .body-program___comments-block .form-page__wrapper--third .body-program__form__title {
        min-width: 162px;
        max-width: 162px;
        font-size: 24px;
        line-height: 24px;
        top: -19px;
        left: 0px; }

    .body-program___comments-block .form-page__wrapper--third {
        padding-top: 39px;
        padding-bottom: 49px; }

    .body-program___comments-block .form-page__wrapper--third .body-programm__textarea {
        font-size: 16px; }

    .body-program___extra-block .form-page__wrapper--four .body-program__form__title {
        min-width: 184px;
        max-width: 184px;
        font-size: 24px;
        line-height: 24px;
        top: -18px;
        left: -2px; }

    .body-program___extra-block .form-page__wrapper--four {
        padding-top: 39px;
        padding-bottom: 34px;
        grid-template-columns: 1fr;
        grid-row-gap: 13px; }

    .body-program___extra-block .form-page__wrapper--four .extra-block__button {
        min-width: 100%;
        max-width: 100%; }

    .body-program___extra-block .form-page__wrapper--four .extra-block__subtitle {
        margin: 0 auto;
        padding-left: 58px; } }











