.form-page {
    .page-header {
        display: grid;
        position: relative;
        grid-template-rows: 168px; }
    .page-header__contents {
        grid-template-rows: minmax(168px, auto) 0px; }

    .page-header::before {
        display: none; }
    .page-header::after {
        display: none; }
    .header-page__menu li a {
        color: $light-black; } }


@media screen and (max-width: 1299.98px) {
    .form-page {
        .page-header {
            grid-template-rows: 110px; }
        .page-header__contents {
            grid-template-rows: minmax(110px, auto) 0px; } } }

@media screen and (max-width: 767.98px) {
    .form-page {
        .page-header {
            grid-template-rows: 65px; }
        .page-header__contents {
            grid-template-rows: minmax(65px, auto) 0px; } } }

